import React, { Component } from 'react';
import Header from '@/scripts/components/header';
import Footer from '@/scripts/components/footer';
import { Button, Popover, Input, Tooltip } from 'antd';
import { history } from '@/utils/history'
import './index.scss'
import { FormattedMessage } from 'react-intl';
import store from '../../store';
import { wsreconnect, candlefunallction } from '../../action';
import { NavLink } from "react-router-dom";
import { Xfn } from '../../../utils/axiosfn';
import Qrcode from "qrcode.react";
import { downloadgeturlfn } from '../../action/tction';
import Bg_xx from './bg_xx';
import Firstloading from '../../components/Firstloading';
import { connect } from "react-redux";
import Echartscont from './echartscont';
import { numberHandle } from '../../../utils/numberHandle';
import Footlogin_box from './footlogin_box';
import Exhibition_platform from './Exhibition_platform';
const { Search } = Input;
@connect(
  state => {
    return {
      ...state.data,
      ...state.datum,
      ...state.bbdata,
      downloadget_url: state.tction.downloadget_url,
      downloadget_url_flg: state.tction.downloadget_url_flg,
    }
  }
)
class Sices extends Component {
  constructor() {
    super()
    this.state = {
      imgarr: {
        banner02: require('../../img/shouji.png'),
        banner03: require('../../img/android.png'),
        banner04: require('../../img/ios.png'),
        banner05: require('../../img/bulletin.png'),
        banner06: require('../../img/slices/yuhangyuan.png'),
        banner07: require('../../img/home_bg.png'),
      },
      dataRes: null,
      flg: true,
      content: 'https://test-gte.s3-ap-northeast-1.amazonaws.com/%E5%AE%89%E5%8D%93/APP_GTE.apk',
      information: [],
      isInsement: true,
      input_acc: '',//
      a: true,
      c2cmin: {},
      exchange_rate_data: {},
      Exhibition_platform1: [
        {
          title: '$4.05B',
          content: '24'
        },
        {
          title: '$110.60B',
          content: '30'
        },
        {
          title: '$1.06T',
          content: '365'
        },
        {
          title: '$1500%',
          content: <FormattedMessage id="Our_Bitcoin_Dollar_products" defaultMessage={' / GTE '} />
        },
        {
          title: '100',
          content: <FormattedMessage id="Number_of_audits_per_second" defaultMessage={'GTE '} />
        },
        {
          title: '0',
          content: <FormattedMessage id="BitcoinLostByInvasionOrBlackout" defaultMessage={'GTE '} />
        },
        {
          title: <FormattedMessage id="UniqueProducts" defaultMessage={''} />,
          content: <FormattedMessage id="GTEProvidesUpTo100TimesLeverag" defaultMessage={'LIANHJ  100 '} />
        },
        {
          title: "API",
          content: <FormattedMessage id="OurTradingEngineUsesThe" defaultMessage={''} />
        },
        {
          title: <FormattedMessage id="LeadingSecurity" defaultMessage={''} />,
          content: <FormattedMessage id="GTEAdoptsTheLatestMulti" defaultMessage={' LIANHJ '} />
        },
      ]
    }
  }
  input_acc = (val) => {
    this.setState({
      input_acc: val.target.value
    })
  }
  componentDidMount() {
    const bodys = document.getElementsByTagName("body")[0]
    bodys.className = "theme-dark"
    store.dispatch({ type: "differentiatedtransactions", num: 1 })
    Xfn({
      _u: 'information_url',
      _m: "get",
      _p: {
        type: "2",
        limit: "3"
      }
    }, (res, code) => {
      if (code == 0) {
        this.setState({
          information: res.data.data.rows
        })
      }
    })
    Xfn({
      _u: "c2min_query",
      _m: "get",
      _p: {}
    }, (res, code) => {
      if (code === 0) {
        this.setState({
          c2cmin: res.data.data
        })
      }
    })
    Xfn({
      _u: 'c2crate_query',
      _m: "get",
      _p: {
        source_asset: 'CNY',//  
        target_asset: 'USDT',//  
      }
    }, (res, code) => {
      if (code === 0) {
        this.setState({
          exchange_rate_data: res.data.data
        })
      }
    })
  }
  componentDidUpdate() {
    if (this.props.downloadget_url_flg === 1) {
      for (let i = 0; i < this.props.downloadget_url.app_list.length; i++) {
        if (this.props.downloadget_url.app_list[i].id === "3") {
          this.setState({
            content: this.props.downloadget_url.app_list[i].link_url
          })
        }
      }
      store.dispatch(downloadgeturlfn({
        downloadget_url_flg: 0
      }))
    }
    if ((this.props.ws_connect == 1 || this.props.isLogin == 1) && this.props.bbasset && this.props.asset) {
      if (window.wss.OPEN == 1) {
        if (this.state.isInsement) {
          this.setState({
            isInsement: false
          })
          window.wss.send(JSON.stringify({
            "op": "sub",
            "args": { "instrument_type": "bb", "table": "instrument_all_full", "settle_currency": this.props.bbasset }
          }))

        } else {
          if (this.props.bbsymbolArr.length !== 0) {
            this.sendMessage()
            // for (let i = 0; i < this.props.bbsymbolArr.length; i++) {
            //   window.wss.send(JSON.stringify({
            //     "op": "sub",
            //     "args": { "instrument_type": "bb", "table": "candle", "settle_currency": this.props.bbsymbolArr[i].settle_currency, "symbol": this.props.bbsymbolArr[i].symbol, "interval": "1" }
            //   }))
            // }
            store.dispatch(wsreconnect(0))
          }
        }
      }
    } else {
      if (this.props.isHistory === 0 && this.props.asset) {
        if (window.wss.OPEN == 1) {
          this.sendMessage()
          window.wss.send(JSON.stringify({
            "op": "sub",
            "args": { "instrument_type": "bb", "table": "instrument_all_full", "settle_currency": this.props.bbasset }
          }))
          // for (let i = 0; i < this.props.instrumentArr.length; i++) {
          //   window.wss.send(JSON.stringify({
          //     "op": "sub",
          //     "args": { "instrument_type": "bb", "table": "candle", "settle_currency": this.props.bbsymbolArr[i].settle_currency, "symbol": this.props.bbsymbolArr[i].symbol, "interval": "1" }
          //   }))
          // }
          store.dispatch({ type: "isHistory", isHistory: 1 })
        }
      }
    }

  }
  sendMessage = () => {
    for (let i = 0; i < this.props.bbsymbolArr.length; i++) {
      Xfn({
        _u: "bbcandlehistory",
        _m: "get",
        _p: {
          asset: "USDT",
          symbol: this.props.bbsymbolArr[i].symbol,
          start_time: (new Date().getTime() - 400 * 60 * 1000).toString(), //,
          end_time: (new Date().getTime()).toString(),
          interval: "1"
        }
      }, (res, code) => {
        if (code == 0) {
          store.dispatch(candlefunallction(res.data, this.props.bbsymbolArr[i].symbol))
        }
      })
    }
  }
  componentWillUnmount() {
    window.scrollTo(0, 0)
    store.dispatch({ type: "differentiatedtransactions", num: 0 })
    window.wss.send(JSON.stringify({
      "op": "unsub",
      "args": { "instrument_type": "bb", "table": "instrument_all_full", "settle_currency": this.props.bbasset }
    }))
    // this.disnumout()
    this.setState = (state, callback) => {
      return
    }
  }
  disnumout = () => {
    if (window.wss.OPEN == 1) {
      for (let i = 0; i < this.props.bbsymbolArr.length; i++) {
        window.wss.send(JSON.stringify({
          "op": "unsub",
          "args": { "instrument_type": "bb", "table": "candle", "settle_currency": this.props.asset, "symbol": this.props.bbsymbolArr[i].symbol, "interval": "1" }
        }))
      }

    }
  }
  goumaitiao = (val) => {
    store.dispatch({ type: "goumaitiao", data: val })
    history.push('/BBTradePage')
  }
  render() {
    const {
      imgarr,
      content,
      information,
      c2cmin,
      exchange_rate_data,
      input_acc,
      Exhibition_platform1
    } = this.state
    const {
      k_line_home_page,
       bbinstrumentArr
    } = this.props
    return this.props.a ? <Firstloading></Firstloading> : (<div className="sices-warp">
      <Header></Header>
      <div className="sices-warp-box" style={{ height: 398 }}>
        <div className="vidage">
          <Bg_xx></Bg_xx>
          <div className="img_gb_png_box">
            <img className="img_gb_png" src={imgarr.banner06} alt="" />

          </div>
        </div>
        <div className="img-box" >
          <div className="hezi clear">
            <div className="hezi-left" >
              <div className="x" style={{ marginTop: localStorage.language == "en" ? "71px" : "91px" }}>
                <FormattedMessage id="Next_Generation_Encrypted_Asset_Trading_Platform" defaultMessage={''} />
              </div>
              <div className="g">
                <FormattedMessage id="Welcome_to_the_most" defaultMessage={'100'} />
              </div>
              <div className="b">
                <Button className="bglanse" onClick={() => {
                  store.dispatch({ type: "isHistory", isHistory: 1 })
                  history.push('/BBTradePage')
                }}>
                  <FormattedMessage id="View_Real_time_Market" defaultMessage={''} />
                </Button>
              </div>
            </div>

          </div>

        </div>


      </div>
      <div className="c2cjioaiyisj">
        <div className="c3ctiaozhuan_box clear">
          <div className="left">
            <div>
              
            </div>
            <div className="lefttext">
              {exchange_rate_data.exchange_rate && numberHandle((1 / exchange_rate_data.exchange_rate + exchange_rate_data.fee * 1), 2, 2)}CNY/USDT
            </div>
          </div>
          <div className="right clear">
            <Search
              placeholder={c2cmin.min_in && c2cmin.min_in + "USDT"}
              enterButton={" "}
              style={{ height: 44 }}
              size="large"
              suffix={
                <Tooltip className="tooltip-001">
                  USDT
              </Tooltip>}
              onSearch={value => {
                if (localStorage.userInfo) {

                  if (value) {
                    history.push('/C2Cdeal/index?account=' + value)

                  } else {
                    history.push('/C2Cdeal/index')
                  }
                } else {
                  history.push('/login')
                }
              }}
            />

          </div>
        </div>
        <div className="tongzhilan" style={{ textAlign: 'center' }}>
          <div className="tongzhilangg" >
            {information ? information.map((item, index) => {
              if (index < 3) {
                return <a key={item + index} style={{
                  color: "#fff", display: "inline-block", padding: '0 15px',
                  borderLeft: index > 0 ? ' 1px solid #fff' : "",
                  lineHeight: '16px'
                }} href={item.link_url} target="_blank" dangerouslySetInnerHTML={{ __html: item.title }}></a>
              }
            }) : ""}
          </div>

        </div>
      </div>
      <div className="jiaoyiwss_box">
        <div className="cneten_box">
          <div className="tiole">
            
          </div>
          <div className="cow_box">
            <div className="cow_box_title">
              <div className="tr" style={{ width: 233 }}>USDT</div>
              <div className="tr" style={{ width: 238 }}></div>
              <div className="tr" style={{ width: 313, textAlign: "center" }}></div>
              <div className="tr" style={{ width: 233, textAlign: "center" }}></div>
              <div className="tr" style={{ textAlign: 'right', width: 233 }}></div>
            </div>
            {
              bbinstrumentArr.length > 0 && bbinstrumentArr.map((item, index) => {
                return <div onClick={() => this.goumaitiao(item.symbol)} className="instrument" key={item + index}>
                  <div className="tr" style={{ width: 233, color: "#fff" }}>
                    <img src={item.icon_url} alt="" />
                    <span className="bb">
                      {item.symbol.split('/')[0]}
                    </span>
                    <span className="zc">
                      {item.symbol_chinese}
                    </span>
                  </div>
                  <div className="tr" style={{ width: 238, color: "#fff" }}>
                    $ {item.last_price}


                  </div>
                  <div className="tr" style={{ width: 313, color: item.change_rate_24h && item.change_rate_24h >= 0 ? "#26994E" : "#E53F39", textAlign: "center" }}>

                    {
                      item.change_rate_24h && item.change_rate_24h >= 0 ? "+ " + String(item.change_rate_24h * 100).replace(/^(.*\..{2}).*$/, "$1") + '%' : String(item.change_rate_24h * 100).replace(/^(.*\..{2}).*$/, "$1") + '%'
                    }

                  </div>
                  <div className="tr" style={{ width: 233 }}>
                    <Echartscont _id={"chartmain" + index} dataRes={k_line_home_page[item.symbol]} hhv={item.change_rate_24h} candle={this.props.candles[item.symbol] && this.props.candles[item.symbol].data}></Echartscont>

                  </div>
                  <div className="tr" style={{ textAlign: 'right', width: 233 }}> <Button ></Button></div>

                </div>
              })
            }
          </div>
        </div>
      </div>
      <div className="bottom-box">
        <div className="flacol clear" >
          {
            Exhibition_platform1.map((item, index) => {
              return <Exhibition_platform data={item} key={item + index}></Exhibition_platform>
            })
          }
        </div>


      </div>
      <div className="footdkdkdk">
        <div className="warp_img_se">
          <div className="toofot">
            <div className="left">
              <div className="capsn-1">
                <FormattedMessage id="AllPlatformTerminalAccess" defaultMessage={''} />
              </div>
              <div className="capsn-2">
                iOSAndroidWeb   <FormattedMessage id="MultiplePlatformsSupportFullBusinessFunctions" defaultMessage={''} />
              </div>
            </div>
            <div className="right">
              <div className="imgclear clear">
                <div>
                  <img src={imgarr.banner02} alt="" />
                </div>
                <div>
                  <Popover content={<Qrcode value={content} size={100} />} >
                    <div className="imga clear">
                      <img src={imgarr.banner03} alt="" />
                      <FormattedMessage id="DownloadForAndroid" defaultMessage={'Android'} />
                    </div>
                  </Popover>
                  <div className="imga clear">
                    <img src={imgarr.banner04} alt="" />
                    <NavLink className="ios_Download" to="/iosdownload" >
                      <FormattedMessage id="DownloadiOSVersion" defaultMessage={'iOS'} />
                    </NavLink>
                  </div>
                </div>
              </div>

            </div>
          </div>
          <div className="img_box_sices">
            <img src={imgarr.banner07} alt="" />
          </div>
        </div>
      </div>
      <Footlogin_box input_accfn={this.input_acc} input_acc={input_acc}></Footlogin_box>
      <Footer></Footer>
    </div >
    )




  }
}
export default Sices;