
//  
export const numberHandle = (num, type, n) => {
  if (type == 1) {//
    if (!num) {
      return "--"
    }
    num = num * 1
    return num.toLocaleString();
  }
  if (type == 2) { //2
    if (!n) {
      n = 2
    }
    num = num.toFixed(n);
    return num
  }
  if (type == 3) { //
    var numdd = new RegExp(`^(.*\\..{${n}}).*$`)
    num = num.replace(numdd, "$1")
    return num
  }

  // var n = 2
  // var numdd = new RegExp(`^(.*\\..{${n}}).*$`)
  // num = num.replace(numdd, "$1")
  // n  
}

